.detailsBox{
    display: flex;
    padding-right: 24px;
    padding-left: 24px;
}


.backButton{
    padding: 8px 16px;
    margin-left: 24px;
    margin-bottom: 20px;
    border-radius: 2px;
    background-color: #3f51b5;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    display: inline-block;
    color: #fff;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    min-width: 180px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  }
  
  .Button:hover,
  .Button:focus {
    background-color: #303f9f;
  }

  .imgDetails{
    width: 250px;
    height: auto;
  }

  .details{
    padding-left: 20px;
  }

  .title{
    margin: 0;
  }

  .detailsBoxAdd{
    padding-right: 24px;
    padding-left: 24px;  
}

.addList{
    display: flex;
}

    .addItem{
        text-decoration: none;
        color: #3f51b5;
        cursor: pointer;
        font-family: inherit;
        font-size: 18px;
        line-height: 24px;
        font-style: normal;
        font-weight: 500;
        min-width: 180px;
        margin-left: 10px;
    }
